.feature-list {
  display: flex;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 0;
  width: 100%;
  flex-wrap: wrap;
  column-gap: 52px;
  row-gap: 55px;
  padding: 92px 0 56px 20px;
  background-color: $color-cerulean-blue;
  border-radius: 20px;
  list-style-type: none;

  @include vp-1199 {
    padding-top: 62px;
    padding-bottom: 40px;
    padding-left: 20px;
    column-gap: 50px;
    row-gap: 42px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 22px;
    padding-top: 42px;
    padding-right: 20px;
    padding-left: 30px;
  }
}

.feature-list__item {
  position: relative;
  width: 39.2%;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 8px;
  padding: 30px 5px 28px 25px;
  border: 1px solid $color-default-white;
  border-radius: 16px;
  color: $color-default-white;

  &--more {
    column-gap: 19px;
    padding-right: 26px;
    padding-left: 40px;
  }

  @include vp-1199 {
    width: 40.2%;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: 25px;

    &--more {
      padding-left: 29px;
    }
  }

  @include vp-767 {
    width: 100%;
    padding-top: 19.5px;
    padding-right: 15px;
    padding-bottom: 23px;
    padding-left: 19px;
  }
}

.feature-list__img {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  position: absolute;
  top: -22.7px;
  left: -21px;
  background-color: $color-cerulean-blue;

  & svg {
    @include vp-767 {
      width: 18px;
      height: 18px;
    }
  }
}

.feature-list__subheader {
  max-width: 172px;
  margin-top: 0;
  margin-bottom: 0;
  letter-spacing: 0.3px;
  font-weight: 400;
  font-size: 26px;
  line-height: 30px;
  text-transform: uppercase;

  @include vp-1199 {
    max-width: 133px;
    font-size: 20px;
    line-height: 22px;
    margin-bottom: 7.5px;
  }

  @include vp-767 {
    max-width: none;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-bottom: 4px;
  }
}

.feature-list__info {
  width: 44%;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 300;
  line-height: 20px;

  @include vp-1199 {
    width: 91%;
  }

  @include vp-767 {
    font-size: 14px;
    width: 83%;
  }
}
